.tbc-main{
    width: 100%;
    padding: 20px;
    position: relative;
    margin-bottom: 10px;
    border-radius: 10px;
    box-sizing: border-box;
    color: black;
    background-color: white;
}
.tbc-head{
    display: flex;
}
.tbc-head p{
    margin: 0;
    flex: 1 1 auto;
    margin-right: 35px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    letter-spacing: 0.5px;
}
.tbc-body{
    width: 100%;
    margin-top: 15px;
    align-items: center;
}
.tbc-body p{
    margin: 0;
    font-size: 14px;
    color: black;
}
.tbc-button-box{
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.tbc-button-box span{
    flex: 1 1 auto;
    color: grey;
    font-size: 15px;
}

.tbc-button-box button{
    height: 100%;
    width: 100px;
    height: 30px;
    font-size: 12px;
    border-radius: 25px;
    background: transparent;
    margin: 10px 0px 0px 10px;
    color: black;
    border: 1px solid rgb(100, 100, 100);
}
.tbc-button-box button:hover{
    background: whitesmoke;
    color: rgb(0, 140, 50);
    border: 1px solid rgb(0, 140, 50);
}