.tr-main{
    width: 98%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    border-left: 1px solid grey;
    animation: slide-down 0.3s ease;
}

@keyframes slide-down {
    0% {
        opacity: 0;
        border-color: transparent;
        transform: translateY(-40px);
    }
    65%{
        border-color: transparent;
    }
    95%{
        border-color: lightgray;
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
@keyframes expandchildtrack {
    from {
        border: 7px 25px 25px 7px;
    }
    to {
        border-radius: 7px;
    }
}

.tr-bottom{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.tr-top{
    width: 99%;
    display: flex;
    margin-top: 10px;
    cursor: pointer;
    flex-direction: column;
    padding: 0px 12px 0px 10px;
    border-radius: 7px 25px 25px 7px;
    background: white;
}
.tr-top-active{
    width: 99%;
    display: flex;
    margin-top: 10px;
    cursor: pointer;
    border-radius: 7px;
    background: white;
    flex-direction: column;
    padding: 0px 12px 0px 10px;
    animation: expandchildtrack 10s ease;
}

.tr-top-1{
    width: 100%;
    height: 50px;
    display: flex;
    cursor: pointer;
    position: relative;
    align-items: center;
}

.tr-top-1 span{
    width: 0; 
    height: 0;
    left: -15px;
    position: absolute;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent; 
    border-right: 10px solid white;
}

.tr-top-1 p{
    margin: 0;
    color: black;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    font-size: 0.9rem;
    flex: 1 1 auto;
}

#tr-black-dot{
    opacity: 0.6;
}

.tr-bar-icon{
    font-weight: 00;
    font-size: 18px;
    color: darkgreen;
    left: 15px;
}
.tr-top-1 a{
    text-decoration: none;
}
.tr-bar-btn{
    font-size: 13px;
    color: darkblue;
    right: 15px;
}
.tr-top-2{
    width: 100%;
    margin-bottom: 15px;
}
.tr-top-2 p{
    color: grey;
    font-size: 0.85rem;
    margin-top: 0;
}
.tr-top-2-btn{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.tr-top-2-btn span{
    font-size: 14px;
    color: gray;
    margin-left: 10px;
}
.tr-top-2-btn button{
    height: 32px;
    font-size: 12px;
    margin-left: 10px;
    border-radius: 15px;
    padding: 0px 15px 0px 15px;
    background: transparent;
    border: 1px solid grey;
}